import React, { useContext } from 'react';
import QuizContext from '../../../context/QuizContext';
import LocalStorageHelper from '../../../core/helpers/localStorageHelper';
import { QUIZ } from '../../../core/constants/actionTypes';
import { JavaScriptQuestions } from '../../../questions-data/javaScriptQuestions';
import { useAlert } from 'react-alert';

const NextQuiz = () => {
    const { state, dispatch } = useContext(QuizContext);
    const alert = useAlert()
    const { answers, currentQuestion, currentAnswers, exactQuestion } = state;
    const questionData = JavaScriptQuestions[LocalStorageHelper.getItem('currentQuestion')];

    const next = () => {
        if(!currentAnswers) {
            alert.error("Place select an option");
            return;
        }
        if(currentAnswers === questionData.correct_answer) {
            dispatch({
                type: QUIZ.SET_EXACT_QUESTION,
                payload: exactQuestion + 1
            })
        }
        const index =  answers.findIndex(({questionId}) => {
            return questionId === questionData.id
        });

        if(index !== -1) {
            answers.splice(index, 1)
        };
        dispatch({
            type: QUIZ.SET_ANSWERS,
            payload: [...answers, {
                    questionId: questionData.id,
                    answer: currentAnswers
            }]
        })
        dispatch({type: QUIZ.SET_CURRENT_ANSWER, payload: ''})
        if(currentQuestion + 1 < JavaScriptQuestions.length) {
            dispatch({
                type: QUIZ.SET_CURRENT_QUESTION,
                payload: currentQuestion + 1
            })
            return;
        }
        dispatch({type: QUIZ.SET_SHOW_RESULTS, payload: true})
    }; 
    return (
        <div>
            <button
                 className={'btn btn-primary'}
                 onClick={next}
            >
                Continue quiz 
                
            </button>
        </div>
    )
};
export default NextQuiz;