import React, { useContext, useState, useEffect, useRef } from 'react';
import QuizContext from '../../../context/QuizContext';
import CodeEditor from '../editor';
import { useOnClickOutside } from '../../../core/Hooks/useOnClickOutside';
import 'prismjs/components/prism-clike';
import 'prismjs/components/prism-javascript';
import './index.css';

const Question = () => {
    const [questionState, setQuestionState] = useState('')
    const editorCode = useRef(null);
    const { state: { currentQuestion, questions,  } } = useContext(QuizContext);
    const questionCode = questions[currentQuestion].questionCode;
    const question = questions[currentQuestion].question;
    
    useEffect(() => {
        setQuestionState(questionCode)
    }, [questionCode]);

    const bringItBackQuestionCode = () => {
        setQuestionState(questionCode)
    }
    useOnClickOutside(editorCode, bringItBackQuestionCode)

    return (
        <>
            <div 
                className='question-code-editor' 
                ref={editorCode}            
            >
                <CodeEditor 
                    isOnChange={true}
                    questionCode={questionState} 
                    onSetQuestionState={setQuestionState}
                    fontSize={20}
                />
            </div>
            <p>{question}</p>
        </>
    )
};

export default Question;