import React, { useContext, useState, useEffect } from 'react';
import { JavaScriptQuestions } from '../../../questions-data/javaScriptQuestions';
import QuizContext from '../../../context/QuizContext';
import CodeEditor from '../editor';
import SendResult from '../../components/send-result';
import { QUIZ } from '../../../core/constants/actionTypes';
import './index.css';

const QuestionResult = ({answers}) => {
    const [isSendMessage, setIsSendMessage] = useState(false);
    const { dispatch, state } = useContext(QuizContext);
    const allQuestionsCount = JavaScriptQuestions.length;
    const [questionId, setQuestionId] = useState(null);
    const { exactQuestion } = state;
    const percent =  Math.round(exactQuestion/allQuestionsCount*100)
    
    const restart = () => {
        dispatch({type: QUIZ.RESET_QUIZ});
        localStorage.clear();
    };  

    const renderQuestionInfo = (currentId, selected_answer, question) => {
        const { correct_answer } = question;
        const correctAnswer = question[`answer_${correct_answer}`];
        const selectedAnswer = question[`answer_${selected_answer}`];
        return (
            <div 
                className={`${currentId === questionId ? 'question-info-content-visible' : ''} question-info-content`}
            >
            <button 
                className={`${setQuestionId ? 'active-close-icon': ''}
                    btn-question-info question-icon-close
                `}
                onClick={() => setQuestionId(null)}
            >
                <i className="fa fa-remove" ></i>
            </button>
                
            <div className={'question-info-desktop'}>
                <div>
                    <h2>Correct Answer</h2>
                    <p>{correctAnswer}</p>

                    <h2>Selected Answer</h2>
                    <p>{selectedAnswer}</p>
                </div>
            </div>
            </div>
        )
    };

    const renderResultsData = () => {
        return answers.map(answer => {
            const question = JavaScriptQuestions.find(question => question.id === answer.questionId);
            return (
                <div 
                    key={question.id} 
                    className={`${question.correct_answer === answer.answer ? 'correct' : 'failed'} `}
                    >
                    <button 
                        className={'btn-question-info question-icon-info'}
                        onClick={() => setQuestionId(question.id)}
                    >
                        <i className="fa fa-info-circle" ></i>
                    </button>
                    <div className='question-info'>
                        <span>{question.id}</span> 
                    </div>
                    <CodeEditor 
                        isOnChange={false}
                        questionCode={question.questionCode}
                        fontSize={16}
                    />
                    {renderQuestionInfo(question.id, answer.answer, question)}
                </div>
            )
        })
    }



    return (
        <div className={'container result'}>
            {
                !isSendMessage && (
                    <SendResult 
                        percent={percent}
                        exactQuestion={exactQuestion}
                        allQuestionsCount={allQuestionsCount}
                        onSetIsSendMessage={setIsSendMessage}
                    />
                )
            }


            {
                isSendMessage && (
                    <>
                        <div className='question-result-info'>
                            <h2>Result</h2>
                            <p>You Scored {percent}%</p>
                            <p> {exactQuestion} of <span>{allQuestionsCount}</span></p>
                        </div>
                        <div className='question-result'>
                            {renderResultsData()}
                        </div>
                        <button  
                            className={'btn btn-primary'}
                            onClick={restart}
                        >
                            Restart
                        </button>
                    </>
                )
            }
        </div>
    )
};

export default QuestionResult;
 