import React, { useContext, useState, useEffect } from 'react';
import Answer from '../answer';
import QuizContext from '../../../context/QuizContext';
const Answers = () => {
    const [selectedAnswer, setSelectedAnswer] = useState(null);
    const { state, dispatch } = useContext(QuizContext);
    const { answers, questions, currentQuestion, currentAnswers } = state;
    const question = questions[currentQuestion];
    const { answer_a, answer_b, answer_c, answer_d } = question;

    useEffect(() => {
        setSelectedAnswer(getSelectedAnswer())
    }, [currentQuestion]);

    const getSelectedAnswer = () => {
        if(answers[currentQuestion] ) {
         return answers[currentQuestion].answer 
        }
    }

    return (
        <div className='answer-content'>
            <Answer 
                letter={'a'} 
                answer={answer_a} 
                selected={currentAnswers === 'a' || 'a' === selectedAnswer}
                dispatch={dispatch}
                onSetSelectedAnswer={setSelectedAnswer}
            />
            <Answer 
                letter={'b'} 
                answer={answer_b} 
                selected={currentAnswers === 'b' || 'b' === selectedAnswer}
                dispatch={dispatch}
                onSetSelectedAnswer={setSelectedAnswer}
            />
            <Answer 
                letter={'c'} 
                answer={answer_c} 
                selected={currentAnswers === 'c' || 'c' === selectedAnswer}
                dispatch={dispatch}
                onSetSelectedAnswer={setSelectedAnswer}
            />
            <Answer 
                letter={'d'} 
                answer={answer_d} 
                selected={currentAnswers === 'd' || 'd' === selectedAnswer}
                dispatch={dispatch}
                onSetSelectedAnswer={setSelectedAnswer}
            />
        </div>
    )
};
export default Answers;