import React, { useContext, useEffect } from 'react';
import QuizContext from '../../../context/QuizContext';
import QuestionResult from '../../components/question-result';
import Progress from '../../components/progress';
import Question from '../../components/question';
import Answers from '../../components/answers';
import GoBackQuiz from '../../components/goBackQuiz';
import NextQuiz from '../../components/nextQuiz';
import { JavaScriptQuestions } from '../../../questions-data/javaScriptQuestions';
import { useHistory } from "react-router-dom";
import Logout from '../../components/logout';
const Quiz = () => {
    const { state: { showResults, answers, currentQuestion, isAuth } } = useContext(QuizContext);
    let history = useHistory();

    useEffect(() => {
        if(!isAuth) {
            history.push('/login')
        }
    }, [isAuth]);

    const renderQuestion = () => {
        if(showResults) {            
            return (
                <QuestionResult answers={answers}/>
            )
        }else {
            return (
                <div> 
                    <Logout />
                    <Progress 
                        total={JavaScriptQuestions.length}
                        current={currentQuestion}
                    />
                    <div className={'container'}>
                        <Question />
                        <Answers />
                        <div className={'next-and-back-button'}>
                            <NextQuiz />
                            <GoBackQuiz />
                        </div>
                    </div>
                </div>
            )
        }
    }
    return (
        <div>
            {renderQuestion()}
        </div>
    )
};
export default Quiz;